import React from 'react'

const ShowItem =(props)=>{
    let {courses , deletCourse ,updataCourse} =props;
    let len = courses.length;
    const ListItem= len ? (
        courses.map(function (course ,index) {
            return (
                <div key={index}>
                    <div className='row'>
                        <p>
                            {course.nameCourse}
                        </p>
                        <button onClick={()=> deletCourse(course.id)} id='delete'>Delete</button>
                        {/*<button onClick={()=> updataCourse(course.id)}>Updata</button>*/}
                    </div>
                </div>
            )
        })
    ) : (
        <p>NO Course to show</p>
    )

    return(
       <div className='showData'>
            {ListItem}
       </div>
    )
}

export default ShowItem