import React , {Component} from 'react'


class AddCourses extends Component{
    state={
        nameCourse:''
    }

    handelchange=(e)=>{
        this.setState({
            [e.target.id]:e.target.value.toUpperCase()
        })
    }

    handelsubmit=(e)=>{
        e.preventDefault();
        if (this.state.nameCourse =="")
        {
            alert('please fii the nameCourse')
        }else
        {
            //الاتنين دول للتوضيح
            // this.state.id=0;
            // this.state.vss='9mm'
            //ودي كل مهمتها انها تبعتلي القيم الي انا حطتها في الstate لل state  الي في ال app الرئيسي
            this.props.addItem(this.state);
            //كل مهمه دي انها تفضلي الstate عشان لما اجي ابعت مبعتش القديم القديمه تاني
            this.setState({
                nameCourse:''
            })
        }
    }

    render() {
        return (
            <div className='addCourses'>
                <form onSubmit={this.handelsubmit}>
                    <input
                        type='text'
                        placeholder='Enter Your Courses...'
                        onChange={this.handelchange}
                        id='nameCourse'
                        value={this.state.nameCourse}/>
                    <input type='submit' id='Add' value='add Course'/>
                </form>
            </div>
        );
    }
}

export default AddCourses