import React ,{Component} from 'react';
import './App.css';
import AddCourses from "./Component/AddCourses/AddCourses";
import ShowItem from "./Component/ShowItem/ShowItem";
class App extends Component{
    state ={
        courses:[]
    }

    addItem =(e)=>{

        let courses=this.state.courses;
        courses.push(e)
        this.setState({
            courses
        })
    }
    deletCourse=(id)=>{
        let courses=this.state.courses.filter(course=>{
            return course.id !==id
        })
        this.setState({courses})
    }
    // updataCourse=(e)=>{
    //
    // }
    render() {
        return (
            <div className="App">
                <div className="box">
                    <h1>ADD COURSES</h1>
                    <AddCourses addItem={this.addItem} />
                    {/*updataCourse={this.updataCourse}*/}
                    <ShowItem courses={this.state.courses} deletCourse={this.deletCourse}  />
                </div>
            </div>
        );
    }
}

export default App;
